import {ISelectableObject} from "./ISelectableObject";

/**
 * 单选组，同一时刻组中的元素只允许最多一个被选中
 */
export class SelectableGroup {

    elements: ISelectableObject[] = [];
    selectedIndex: number = -1;
    allowNoSelection: boolean;

    /**
     * 创建一个选择组
     * @param allowNoSelection 是否允许一个元素都不选（允许取消当前唯一的选择）
     */
    constructor(allowNoSelection: boolean = true) {
        this.allowNoSelection = allowNoSelection;
    }

    /**
     * 添加一个元素
     */
    public addElement(e: ISelectableObject): void {
        if (this.indexOf(e) != -1) return;
        e.selectableGroup = this;
        this.elements.push(e);
        if (this.size() == 1 && !this.allowNoSelection) {
            this.select(e);
        }
    }

    /**
     * 删除一个元素
     */
    public removeElement(e: ISelectableObject): void {
        let idx = this.indexOf(e);
        if (idx == -1) return;
        if (this.isSelected(e)) {
            this.selectedIndex = -1;
            this.elements.splice(idx, 1);
            e.onDeselected();
            // 如果不允许不选，则自动选第0个
            if (!this.allowNoSelection && this.size() > 0) {
                this.select(this.getElementAt(0));
            }
        } else {
            this.elements.splice(idx, 1);
            if (idx < this.selectedIndex) this.selectedIndex--;
        }
        e.selectableGroup = null;
    }

    /**
     * 查找元素的下标，不存在则返回-1
     */
    public indexOf(e: ISelectableObject): number {
        return this.elements.indexOf(e);
    }


    /**
     * 获取当前单选组里有多少元素
     */
    public size(): number {
        return this.elements.length;
    }

    /**
     * 获取第i个单选组里的元素
     */
    public getElementAt(index: number): ISelectableObject {
        return this.elements[index];
    }

    /**
     * 获取指定对象是否是被选择的那个
     */
    public isSelected(e: ISelectableObject): boolean {
        return this.currentSelection() == e;
    }

    /**
     * 选择指定对象
     */
    public select(e: ISelectableObject): void {
        let newIdx = this.indexOf(e);
        if (newIdx == -1) return;
        if (this.selectedIndex == newIdx) return;//是否应该return？
        let old = this.currentSelection();
        /*
        this.selectedIndex = -1;
        old?.onDeselected();
        this.selectedIndex = newIdx;
        e.onSelected();
        */
        this.selectedIndex = newIdx;
        old?.onDeselected();
        e.onSelected();
    }

    /**
     * 取消选择指定对象
     */
    public deselect(e: ISelectableObject): void {
        if(!e) return;
        let newIdx = this.indexOf(e);
        if (newIdx == -1) return;
        if (!this.allowNoSelection) return;
        this.selectedIndex = -1;
        e.onDeselected();
    }

    /**
     * 获取当前选择的对象
     */
    public currentSelection(): ISelectableObject | null {
        if (this.selectedIndex == -1) return null;
        return this.getElementAt(this.selectedIndex);
    }

}
