import Label = cc.Label;
import {i18nMgr} from "../Managers/I18nManager";
import menu = cc._decorator.menu;

const {ccclass, property} = cc._decorator;

/**
 * 用法：在结点上添加cc.Label后，再添加cc.LocalizedLabel
 */
@ccclass
@menu("其他组件/多语言")
export class LocalizedLabel extends cc.Component {
    /**
     * 绑定的Label
     */
    public label: Label;

    /**
     * 当前显示的文字的未翻译版本
     */
    public prevRawText: string;

    /**
     * 当前显示的文字的翻译版本
     */
    public prevLocalizedText: string;

    /**
     * 未翻译的字符串
     */
    get string() {
        return this.prevRawText;
    }

    /**
     * 设置翻译前的字符串
     * @param value
     */
    set string(value: string) {
        this.prevRawText = value;
        this.prevLocalizedText = this.label.string = i18nMgr().t(value);
    }

    protected start() {
        i18nMgr().registerLabel(this);
    }

    protected onEnable() {
        this.label = this.getComponent(Label);
        if (!this.label) {
            console.log("i18nLabel没有对应的Label");
            return;
        }
        this.string = this.label.string;
    }

    protected onDisable() {
        //i18nMgr().unregisterLabel(this);
    }

    /**
     * 根据最新的语言设置刷新一次当前的文字
     */
    public updateText() {
        this.string = this.prevRawText;
    }

    protected update(dt: number) {
        if (this.label.string != this.prevLocalizedText) {
            this.string = this.label.string;
        }
    }

    protected lateUpdate(dt: number) {
        this.update(dt);
    }
}
