import {BaseConfig, config} from "../../CoreModule/Data/BaseConfig";
import {field} from "../../CoreModule/Data/BaseData";

@config("PartConfig")
export default class PartConfig extends BaseConfig {

	// @field
	// srcPartIds: number[] = [];
	//
	// @field
	// tarPartIds: number[] = [];
	//
	@field // 槽层次配置
	slotOrders: {[T: string]: number} = {}

	@field // 槽名称映射，用于在不影响骨骼名情况下添加部件类型
	slotNameMap: {[T: string]: string} = {}

	@field // PartType → HeroType 的缩放设置
	pHtScale: number[][][] = [
		// 三个下标分别表示：候选部件A的PartTypeId, A的HeroTypeId, 当前HeroTypeId
		// 当玩家选择A时，应用ptHtScale[A.partTypeId][A.heroTypeId][当前HeroTypeId]的缩放值
		// 缩放值设置为0时，表示互斥

	];

	@field // PartType → Base PartType 的缩放设置，key: oriId-bOriId
	ptBptScale: {[T: string]: number} = {};
	// ptBptScale: [number, number, number][] = [
	// 	// 三个参数分别表示：候选部件A的oriId, 已有部件B的oriId, 缩放值C
	// 	// （需满足前提：A.partType.baseTypeId == B.partTypeId，否则不生效）
	// 	// 当玩家选择A时，如果当前已经装备了B，则A应用C缩放
	// 	// 如果C为0，表示A无法装备到B上，需要在界面中排除掉
	//
	// ];

	@field // PartType → Base PartType 的偏移设置，key: oriId-bOriId
	ptBptOffset: {[T: string]: [number, number]} = {};
	// ptBptOffset: [number, number, [number, number]][] = [
	// 	// 三个参数分别表示：候选部件A的oriId, 已有部件B的oriId, 缩放值C
	// 	// （需满足前提：A.partType.baseTypeId == B.partTypeId，否则不生效）
	// 	// 当玩家选择A时，如果当前已经装备了B，则A应用C偏移量
	// 	// 如果C为0，表示A无法装备到B上，需要在界面中排除掉
	//
	// ];

	@field // PartType → Base PartType 的隐藏设置，key: oriId-bOriId
	ptBptHidden: {[T: string]: string[]} = {};
	// ptBptHidden: [number, number, string[]][] = [
	// 	// 三个参数分别表示：候选部件A的oriId, 已有部件B的oriId, 缩放值C
	// 	// （需满足前提：A.partType.baseTypeId == B.partTypeId，否则不生效）
	// 	// 当玩家选择A时，如果当前已经装备了B，则A应用C隐藏
	//
	// ];

	@field // clothes6问题，key: oriId-hOriId
	clothes6Problem: {[T: string]: boolean} = {};

	@field // 每个body的offset
	footOffset: {[T: number]: [number, number]} = {};

}
