import {Platform, platformMgr} from "../Modules/CoreModule/Managers/PlatformManager";
import {Constructor} from "../Utils/BaseContext";

// 个人信息用途
export const UserInfoDesc = "个人信息仅用于排行榜展示";
// 广告每日次数
export const MaxAdsDayCount = 15;
// 广告UnitID
export const AdsUnitId = 'fruit';

export abstract class BasePlatform {

	public type: Platform;

	protected constructor(type: Platform) {
		this.type = type;
		this.initAds();
	}

	// region 用户信息

	/**
	 * 获取用户数据
	 */
	public async getUserData() {
		return {
			type: this.type,
			openid: await this.getOpenid(),
			userInfo: await this.getUserInfo()
		}
	}

	/**
	 * 获取Openid
	 */
	public abstract getOpenid() : Promise<string>;

	/**
	 * 获取用户信息
	 */
	public abstract getUserInfo() : Promise<any>;

	// endregion

	// region 好友系统

	// endregion

	// region 广告系统

	// TODO: 封装后的代码还需要再次测试

	// protected tryCount: number = 0; // 重试次数
	protected adsEnable: boolean = false; // 广告是否可用

	protected adsData; // 广告数据
	protected adsObject; // 广告对象

	public adsCheckCount = 5; // 最大尝试次数

	// public adsCheckInterval = 500; // 广告检测间隔
	// public adsMaxCheckTime = 5000; // 广告检测最大时间

	/**
	 * 初始化广告
	 */
	protected initAds() {
		this.loadAds().then();
	}

	/**
	 * 拉取一次广告
	 */
	protected async loadAds() {
		// 只读取一次
		try {
			this.adsData = await this.doLoadAds();
			console.log("loadAds: Success: ", this.adsData);
		} catch (e) {
			console.error("loadAds: Error: ", e);
		}

		/*
		try {
			// 调用doLoadAds函数，如果报错会进入catch
			this.adsData = await this.doLoadAds();
		} catch (e) { // 使用同步写法，不用.catch()
			console.error("loadAds: Error: ", e);
			if (this.tryCount++ < MaxAdsRetryCount)
				await this.loadAds();
			else this.tryCount = 0;
		}
		*/
	}

	protected async doLoadAds(): Promise<any> {
		this.onLoad();
		return {};
	}

	protected onLoad() {
		this.adsEnable = true;
	}

	/**
	 * 显示广告
	 * 如果暂时无法显示，将会自动手动获取，直到可以显示为止
	 * 如果失败次数超过限制，抛出异常
	 */
	public async showAds() : Promise<boolean> {
		const res = await this.pendingAds();
		if (!res) throw new Error("暂无广告可用");
		return await this.doShowAds();
	}

	private pendingAds() : Promise<boolean> {
		return new Promise<boolean>((resolve => {
			let adsCheckTime = 0;
			const check = async () => {
				// 先尝试读取广告
				await this.loadAds();

				// 如果广告已经可以用了 或者 广告检查次数超过了
				if (this.adsEnable ||
					++adsCheckTime >= this.adsCheckCount)
					resolve(this.adsEnable); // 返回当前可用结果

				// 否则，再次尝试获取并检查
				else check().then();
			}
			check().then();
		}));
	}

	protected async doShowAds() : Promise<boolean> {
		this.adsEnable = false;
		return true;
	}

	// endregion

}

export abstract class MiniGamePlatform extends BasePlatform {

	// region 广告

	protected abstract createAdsObject(adUnitId);

	protected initAds() {
		this.adsObject = this.createAdsObject(AdsUnitId);
		this.adsObject.onLoad(this.onLoad.bind(this));
		// this.adsObject.onError(() => this.tryCount++);
		this.loadAds().then();

	}

	/*
	// 手动拉取广告
	private async loadAds() {
		this.adsEnable = false;
		try {
			this.adsData = await this.adsObject.load();
			console.log("loadAds: Success: ", this.adsData);
		} catch (e) { // 使用同步写法，不用.catch()
			console.error("loadAds: Error: ", e);
			this.tryCount < MaxAdsRetryCount ?
				this.loadAds() : this.tryCount = 0;
		}
	}
	*/

	protected async doLoadAds(): Promise<any> {
		this.adsEnable = false;
		return await this.adsObject.load();
	}

	protected async doShowAds(): Promise<boolean> {
		if (!await super.doShowAds()) return false;

		await this.adsObject.show();

		return await new Promise<boolean>(((resolve, reject) => {
			this.adsObject.onClose(res => {
				this.adsObject.offClose();
				resolve(res && res.isEnded || !res);
			});
		}))
	}

	// endregion

}

export function platform(type: Platform) {
	return <T extends BasePlatform>(clazz: Constructor<T>) => {
		platformMgr().registerPlatform(type, clazz);
		return clazz;
	}
}
