import {BaseData} from "../../CoreModule/Data/BaseData";
import {Web3NFTComponentData} from "../../NFTModule/Utils/Web3";
import {Part, partRepo} from "./Part";

export class PartInfo extends BaseData {

	public partId: number; // 组件ID
	public name: string = ""; // 组件名称
	public partTypeId: number = -1; // 组件类别
	public price: number = 0; // 组件价格
	public stock: number = 0; // 组件库存
	public url: string = ""; // 图片地址
	// public ban: boolean = false; // 冲突

	public rawData: Web3NFTComponentData; // 原始数据

	private _part: Part;
	public get part(): Part {
		return this._part ||= partRepo().getById(this.partId);
	}
}
