import {BaseData, field, GameData} from "../../CoreModule/Data/BaseData";
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {Constructor} from "../../../Utils/BaseContext";
import {data, StaticData} from "../../CoreModule/Data/StaticData";

@data("Job")
export class Job extends StaticData {

	@field(Number)
	public time: number = -1; // 打工时间
	@field(Number)
	public maxLevel: number = 100; // 最大等级
	@field([Number])
	public unlockParams: number[] = []; // 解锁需求参数
	@field([Number])
	public profitParams: number[] = []; // 收益参数
	@field([Number])
	public upgradeParams: number[] = []; // 升级消耗参数

	/**
	 * 解锁金币需求
	 */
	public get unlockGold() { return this.unlockParams[1]; }

	/**
	 * 回城次数需求
	 */
	public get unlockReturnTime() { return this.unlockParams[0] || 0; }

	/**
	 * 获取实际收益
	 */
	public profit(level: number): number {
		// TODO: 完善收益算法
		return this.profitParams[0] + this.profitParams[1] * level;
	}

	/**
	 * 获取实际升级金币需求
	 */
	public upgradeGold(level: number): number {
		// TODO: 完善升级花费算法
		return this.upgradeParams[0] + this.upgradeParams[1] * level;
	}

}

export function jobRepo() {
	return getRepository(JobRepo);
}

@repository
export class JobRepo extends BaseRepository<Job> {

	get clazz(): Constructor<Job> { return Job; }

}
