import {data, StaticData} from "../../CoreModule/Data/StaticData";
import {BaseRepository, getRepository, repository} from "../../CoreModule/Data/BaseRepository";
import {Constructor} from "../../../Utils/BaseContext";
import {field} from "../../CoreModule/Data/BaseData";
import {QualityType} from "./Quality";

// noinspection SpellCheckingInspection
export enum ParamType {
	CP, // Computing Power: 算力
	CPB, // Computing Power Bonus: 算力增幅
	HID, // HIDden: 隐藏属性
	POW, // POWer: 力量
	AGI, // AGIle: 敏捷
	INT, // INTelligence: 智力
	POWB, // POWer Bonus: 力量增幅
	AGIB, // AGIle Bonus: 敏捷增幅
	INTB, // INTelligence Bonus: 智力增幅
	ATK, // ATtacK: 攻击
	CRI, // CRItical: 暴击
	DMG, // Damage: 伤害
	ATKB, // ATtacK Bonus: 攻击增幅
	CRIB, // CRItical Bonus: 暴击增幅
	DMGB, // Damage Bonus: 伤害增幅
	DEF, // DEFense: 防御
	AVO, // AVOidance: 减伤
	EVA, // EVAsion: 闪避
	DEFB, // DEFense Bonus: 防御增幅
	AVOB, // AVOidance Bonus: 减伤增幅
	EVAB, // EVAsion Bonus: 闪避增幅
	MHP, // Max HP: 生命
	SPD, // SPeeD: 速度
	HIT, // HIT: 命中
	MHPB, // Max HP Bonus: 生命增幅
	SPDB, // SPeeD Bonus: 速度增幅
	HITB, // HIT Bonus: 命中增幅
	AB1, // All Bonus 1: 万能增幅1
	AB2, // All Bonus 2: 万能增幅2
	AB3, // All Bonus 3: 万能增幅3
	AB4, // All Bonus 4: 万能增幅4

	// 以下为额外属性
	BP, // Battle Point: 战力
	BPB, // Battle Point Bonus: 战力

}

@data("Param")
export class Param extends StaticData {

	@field(Number)
	public id: ParamType;
	@field(String)
	public attr: string;
	@field
	public default_: number = 0;
	@field
	public min: number = 0;
	@field
	public max: number = 999999;
	@field
	public isPercent: boolean = false;
	@field([Number])
	public bonus: number[];
	@field
	public battlePoint: number = 0;

	/**
	 * 缩写
	 */
	public get shortName() { return ParamType[this.id]; }
}

export function paramRepo() {
	return getRepository(ParamRepo);
}

@repository
export class ParamRepo extends BaseRepository<Param> {

	get clazz(): Constructor<Param> { return Param; }

	getById(id: ParamType): Param {
		return super.getById(id);
	}

	// @ts-ignore
	getByAttr(attr: string): Param {}

}
