import {BaseConfig, config} from "../../CoreModule/Data/BaseConfig";
import {field} from "../../CoreModule/Data/BaseData";

@config("BattleConfig")
export class BattleConfig extends BaseConfig {

	@field // 命中判定参数
	hit = {
		defaultBase: 0.1,
		baseHitRate: 0.97,
		float: [0.9, 1.1]
	}
	@field // 暴击判定参数
	crit = {
		minRate: 0.03,
		damageRate: 2,
	}
	@field
	restraint = {
		// 克制时伤害, 普通伤害, 被克制伤害
		rates: [1, 4, 0.5]
	}
}
