import Sprite = cc.Sprite;
import {i18nMgr} from "../Managers/I18nManager";

const {ccclass, property} = cc._decorator;

/**
 * 用法：在结点上添加cc.Sprite后，再添加cc.i18nSprite
 */
@ccclass
export class LocalizedSprite extends cc.Component {
    /**
     * 绑定的Sprite
     */
    public sprite: Sprite;

    /**
     * 图片名称，仅用于初始化，动态改变请用小写开头的spriteName
     */
    @property
    private SpriteName: string = "";

    /**
     * 未翻译的字符串
     */
    get spriteName() {
        return this.SpriteName;
    }

    /**
     * 设置翻译前的字符串
     * @param value
     */
    set spriteName(value: string) {
        this.SpriteName = value;
        i18nMgr().ts(value).then((val) => {
            this.sprite.spriteFrame = val;
        });
    }

    protected onEnable() {
        this.sprite = this.getComponent(Sprite);
        if (this.sprite == null) {
            console.log("i18nSprite没有对应的Sprite");
            return;
        }
        i18nMgr().registerSprite(this);
        this.spriteName = this.SpriteName;
    }

    protected onDisable() {
        super.onDisable();
        i18nMgr().unregisterSprite(this);
    }

    /**
     * 根据最新的语言设置刷新一次当前的文字
     */
    public updateSprite() {
        this.spriteName = this.spriteName;
    }

    public test33() {
        this.spriteName = this.spriteName == "loc/test" ? "loc/tt" : "loc/test";
    }
}
