import {error, get, post} from "../../CoreModule/Common/CoreCommon";

export class EconomicThrow {

    @error(400001, "持有货币不足") static NotEnoughMoney(d?) {}
    @error(400002, "持有碎片不足") static NotEnoughFragment(d?) {}
    @error(400003, "回城次数不足") static NotEnoughReturn(d?) {}
    @error(400011, "无法解锁打工点") static CanNotUnlockJob(d?) {}
    @error(400012, "打工点已解锁") static JobAlreadyUnlocked(d?) {}
    @error(400013, "打工点未解锁") static JobNotUnlocked(d?) {}
    @error(400014, "到达打工等级上限") static ReachJobMaxLevel(d?) {}
    @error(400015, "领取时间未到") static NotJobProfitTime(d?) {}
    @error(400016, "解锁打工点失败") static UnlockJobError(d?) {}
    @error(400017, "升级打工点失败") static UpgradeJobError(d?) {}
    @error(400018, "领取打工金币失败") static ProfitJobError(d?) {}

}

export class EconomicInterface {

    @post("/job/job/unlock") static UnlockJob(d?): any {}
    @post("/job/job/upgrade") static UpgradeJob(d?): any {}
    @post("/job/job/profit") static ProfitJob(d?): any {}

}
