import {dataPK, field, GameData} from "../../CoreModule/Data/BaseData";
import {ChainId, DefaultChainId, WalletType} from "../../NFTModule/Managers/NFTManager";
import {DynamicData} from "../../CoreModule/Data/DynamicData";

export enum PlayerState {
	Normal, Banned
}

export abstract class PlayerData extends DynamicData {

	@field(String) @dataPK
	public uuid: string;

}

export class Player extends GameData {

	@field
	public uuid: string = ""; // UUID

	@field
	public chainId: ChainId = DefaultChainId; // 链ID
	@field
	public account: string = ""; // 钱包地址
	@field
	public wallet: WalletType = 'Metamask'; // 钱包类型

	@field
	public name: string = ""; // 玩家名
	@field
	public avatarUrl: string = ""; // 头像URL

	@field(Number)
	public createdAt: number;

	@field
	public state: PlayerState = PlayerState.Normal;
	@field(Number)
	public stateTime: number;
	@field(String)
	public stateDesc: string;

	// region 基本操作

	/**
	 * 登陆
	 */
	public login() {

	}

	/**
	 * 登出
	 */
	public logout() {

	}

	// endregion

}
